import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BranchMuseumsServiceProvider } from '../../providers/branch-museums-service/branch-museums-service'
import * as app_globals from '../../app/app-globles';

/**
 * Generated class for the OpeningHoursPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-opening-hours',
  templateUrl: 'opening-hours.html',
})
export class OpeningHoursPage {

   public branches: any;
   public contentURL: any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private branchMuseumsServiceProvider: BranchMuseumsServiceProvider) {
     this.contentURL=app_globals.k_content_url;
   this.branches= branchMuseumsServiceProvider.populateMuseum("en");
   
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad OpeningHoursPage');
  }

}



